import React from "react";
import Nav from "../components/nav";
import { Link } from "react-router-dom";
import userData from "../mocks/users";
import "../css/mypage.css";

const Account = () => {
  const user = userData;
  return (
    <div>
      <Nav />
      <div className="mypage-container">
        {/*    */}
        <div className="mypage-left">
          <div className="menu">
            <h2 className="menu-name">  </h2>
            <ul className="menu-item">
              <li>
                <Link to="/account"> </Link>
              </li>
              <li>
                <Link to="/myposts"> </Link>
              </li>
              <li>
                <Link to="/favorites"> </Link>
              </li>
              <li>
                <Link to="/passwordchange"> </Link>
              </li>
              <li>
                <Link to="/logout"></Link>
              </li>
              <li>
                <Link to="/withdraw"> </Link>
              </li>
            </ul>
          </div>
          <div>
            <h2 className="menu-name"> </h2>
            <ul className="menu-item">
              <li>
                <Link to="/terms">  </Link>
              </li>
              <li>
                <Link to="/privacy">  </Link>
              </li>
            </ul>
          </div>
        </div>

        <div className="account-right">
          <div>
            <h3 id="account_title"> </h3>
          </div>
          {/*  */}
          <div>
            <h5>  </h5>
            <span className="user-id">{user.ID}</span>
          </div>

          {/*  */}
          <div>
            <h5>  </h5>
            <input
              type="text"
              class="input-field"
              maxLength="10"
              name="userjoin_name"
              // placeholder=""
            />
          </div>

          {/*  */}
          <div>
            <h5>  </h5>
            <input
              type="text"
              class="input-field"
              maxLength="10"
              name="userjoin_nickname"
              // placeholder=""
            />
          </div>

          {/*   */}
          <div>
            <h5>   </h5>
            <input
              type="text"
              class="input-field"
              maxLength="11"
              name="userjoin_tel"
            />
          </div>

          <div>
            <button type="submit" id="accountbutton">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Account;
