import React from "react";
import "../css/login.css";
import { NavLink } from "react-router-dom";
import Nav from "../components/nav.js";

const Login = () => {
  return (
    <div>
      <div>
        <Nav />
      </div>
      <form className="loginForm">
        <div>
          <div>
            <h2 id="login_title"></h2>
          </div>
        </div>
        <div>
          <div className="input">
          <h5>  </h5>
            <input
              type="text"
              className="userId"
              id="userId"
              placeholder=""
              autoFocus
            ></input>
            <h5>  </h5>
            <input
              type="password"
              className="password"
              id="password"
              placeholder=""
            ></input>
            <button id="loginBut"></button>
            <div className="link">
              <NavLink to="/findid"> </NavLink>
              <span>&nbsp;|&nbsp;</span>
              <NavLink to="/findpw"> </NavLink>
              <span>&nbsp;|&nbsp;</span>
              <NavLink to="/userjoin"> </NavLink>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
};

export default Login;
