import { useParams } from "react-router-dom";
import Nav from "../components/nav";
import events from "../mocks/events"; //      
import "../css/eventdetail.css";
import React, { useState, useEffect } from "react";

function Eventdetail() {
  const { id } = useParams();
  const [event, setEvent] = useState(null);

  useEffect(() => {
    const foundEvent = events.find((e) => e.id === id);
    setEvent(foundEvent);
  }, [id]);

  if (!event) return <div>    .</div>;

  return (
    <div>
      <div>
        <Nav />
      </div>
      <div className="event-detail">
        <img
          src={event.poster}
          alt={`${event.title} `}
          className="poster"
        />
        <div className="event-info-container">
          <div className="event-content">
            <h3 className="title-center">{event.title}</h3>
            <p>
              <strong>:</strong> {event.start}
            </p>
            <p>
              <strong>:</strong> {event.time}
            </p>
            <p>
              <strong>:</strong> {event.location}
            </p>
            <p>
              <strong>:</strong> {event.seat}
            </p>
            <p>
              <strong>:</strong> {event.cast || " "}
            </p>
            <p>
              <strong>:</strong> {event.price}
            </p>
          </div>
          <div className="event-review">
            <h3 className="title-center"> </h3>
            {event.review && event.review.trim() !== "" ? (
              <p className="review-item">{event.review}</p>
            ) : (
              <p>  .</p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Eventdetail;
