import React from "react";
import "../css/userjoin.css";
import Nav from "../components/nav";

const Userjoin = () => {
  return (
    <div>
      <div>
        <Nav />
      </div>
      <form>
        <div>
          <h2 id="userjoin_title"> </h2>
        </div>
        <div className="userjoin">
          <div>
            {/*  */}
            <div>
              <h5>  </h5>
              <input
                type="text"
                class="input-field-1"
                maxLength="20"
                name="userjoin_id"
                placeholder="7  "
                autoFocus
              />
              <button type="button" id="dupIdCheck">
                
              </button>
            </div>

            {/*  */}
            <div>
              <h5>  </h5>
              <input
                type="password"
                class="input-field-2"
                maxLength="15"
                name="userjoin_password"
                // placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <h5>   </h5>
              <input
                type="password"
                class="input-field-2"
                maxLength="15"
                name="userjoin_pswCheck"
                // placeholder=" "
              />
            </div>

            {/*  */}
            <div>
              <h5>  </h5>
              <input
                type="text"
                class="input-field-2"
                maxLength="10"
                name="userjoin_name"
                // placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <h5>  </h5>
              <input
                type="text"
                class="input-field-2"
                maxLength="10"
                name="userjoin_nickname"
                // placeholder=""
              />
            </div>

            {/*   */}
            <div>
              <h5>   </h5>
              <input
                type="text"
                class="input-field-1"
                maxLength="11"
                name="userjoin_tel"
              />
              <button type="button" id="sendPhoneCode">
                 
              </button>
              {/* ,     */}
            </div>

            {/*  */}
            <div>
              <h5>  </h5>
              <input
                type="text"
                class="input-field-1"
                maxLength="6"
                name="userjoin_phonecode"
              />
              <button type="button" id="verifyPhoneCode">
                 
              </button>
            </div>
          </div>
        </div>

        <div>
          <button type="submit" id="sbtn">
            
          </button>
        </div>
      </form>
    </div>
  );
};

export default Userjoin;
