import React from "react";
import { NavLink } from "react-router-dom";
import "./nav.css"; 
import logo from "../components/logo3.png";

const Nav = () => {
  return (
    <nav className="navbar">
      <div className="nav-left">
        <NavLink to="/">
          <img src={logo} alt="" className="nav-logo" />
        </NavLink>
      </div>
      <div className="nav-right">
        <div className="nav-item-dropdown">
          <NavLink to="/calendarrecords" className="nav-item"> </NavLink>{/*    ,       */}
          <div className="dropdown-content">
            <NavLink to="/calendarrecords" className="dropdown-item">  </NavLink>
            <NavLink to="/calendarfavorites" className="dropdown-item"> </NavLink>
            <NavLink to="/calendarstatistics" className="dropdown-item"> </NavLink>
          </div>
        </div>
        <NavLink to="/findidresult" className="nav-item"></NavLink>
        <NavLink to="/mypage" className="nav-item"></NavLink> {/*   ,       */}
      </div>
    </nav>
  );
};

export default Nav;
