import React from "react";
import Nav from "../components/nav";
import { Link } from "react-router-dom";
// import userData from "../mocks/users";
import "../css/mypage.css";
import poster1 from "../components/poster1.jpg";
import poster2 from "../components/poster2.jpg";

const favoriteShows = [
  { title: "  ", image: poster1 },
  { title: "", image: poster2 },
];

const Favorites = () => {
  // const user = userData;
  return (
    <div>
      <Nav />
      <div className="mypage-container">
        {/*    */}
        <div className="mypage-left">
          <div className="menu">
            <h2 className="menu-name">  </h2>
            <ul className="menu-item">
              <li>
                <Link to="/account"> </Link>
              </li>
              <li>
                <Link to="/myposts"> </Link>
              </li>
              <li>
                <Link to="/favorites"> </Link>
              </li>
              <li>
                <Link to="/passwordchange"> </Link>
              </li>
              <li>
                <Link to="/logout"></Link>
              </li>
              <li>
                <Link to="/withdraw"> </Link>
              </li>
            </ul>
          </div>
          <div>
            <h2 className="menu-name"> </h2>
            <ul className="menu-item">
              <li>
                <Link to="/terms">  </Link>
              </li>
              <li>
                <Link to="/privacy">  </Link>
              </li>
            </ul>
          </div>
        </div>

        <div className="account-right">
          <div>
            <h3 id="account_title"> </h3>
          </div>
          <div className="show-grid">
            {favoriteShows.map((show, index) => (
              <div key={index} className="show-card">
                <img src={show.image} alt={show.title} />
                <p>{show.title}</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Favorites;
